import './App.css';
import React, { useRef, useState } from "react";
import { HashRouter } from 'react-router-dom'
import { Cosmograph, CosmographProvider, CosmographSearch, CosmographHistogram } from '@cosmograph/react';
const data = await import("./data.json")

function Histogram({ hasHierarchy }){
  if (!hasHierarchy) {
    return null;
  }
  return (
      <div style={{display: 'inline-block', 'text-align': 'center', 'vertical-align': 'top', position: 'absolute', bottom: '0%'}}>
      <CosmographHistogram
      accessor={(n) => n.hierarchy}
      barCount={data.nodes.reduce((acc, val) => Math.max(acc, val.hierarchy), 0) + 2}
      style={{'--cosmograph-histogram-bar-color': '#ffffff'}}
      />
        <span style={{display: 'block'}}>Node hierarchy</span>
      </div>);
}

function App() {
  const searchRef = useRef(null)
  const graphRef = useRef(null)
  let nodes = data.nodes
  let links = data.links

  const [isCurved, setIsCurved] = useState(false);
  const [buttonText, setButtonText] = useState('Use Curved Edges');

  const toggleIsCurved = () => {
    setIsCurved(current => !current);
    if (isCurved){
      setButtonText('Use Curved Edges');
    } else {
      setButtonText('Use Straight Edges');
    }
  };

  let hoveredNode = null;

  function labelAccessor(node){
    if (hoveredNode !== null && node.id === hoveredNode.id){
        return node.id
    } else {
      return node.label
    }
  }

  //
  //const clearInput = () => searchRef.current?.clearInput()
  return (
      <HashRouter>{
        <div className="full-screen-div">
          <CosmographProvider nodes={nodes} links={links}>
            <CosmographSearch
                className='search'
                ref={searchRef}
                onSelectResult={(node) => graphRef.current.selectNode(node, true)}
            />
            <Cosmograph
                ref={graphRef}
                // nodes
                nodeColor={"#000000"}
                scaleNodesOnZoom={true}
                nodeSize={1}
                nodeLabelAccessor={labelAccessor}
                showDynamicLabels={true}
                showHoveredNodeLabel={true}
                nodeLabelColor={"#FFFFFF"}
                hoveredNodeLabelColor={"#FFFFFF"}
                hoveredNodeRingColor={"#000000"}
                // links
                renderLinks={true}
                linkColor={"#000000"}
                linkWidth={5}
                linkArrows={true}
                linkVisibilityDistanceRange={[1000, 5000]}
                linkVisibilityMinTransparency={0.75}
                curvedLinks={isCurved}
                curvedLinkWeight={0.5}
                curvedLinkControlPointDistance={1}
                // zoom
                initialZoomLevel={10}
                // background
                backgroundColor={"#F8F8FF"}
                showFPSMonitor={false}
                pixelRatio={2}
                // simulation
                spaceSize={8192}
                simulationLinkDistance={4}
                // style
                style = {{height:'93.8vh'}}
                // events
                onNodeMouseOver={(node, idx, pos, event) => {
                  hoveredNode = node;
                  graphRef.current.restart();
                }}
                onNodeMouseOut={(event) => {
                  hoveredNode = null;
                  graphRef.current.restart();
                }}
                onClick={(node, idx, pos, event) => {
                  if (typeof node !== 'undefined') {
                    graphRef.current.selectNode(node, true);
                  } else {
                    graphRef.current.unselectNodes();
                    searchRef.current.clearInput();
                  }
                }}
            />
            <button className="toggleCurvedButton" onClick={toggleIsCurved} style={{position: 'absolute', top: '7%', left: '0%'}}>{buttonText}</button>
            <Histogram hasHierarchy={data.nodes[0].hierarchy !== null}/>
          </CosmographProvider>
        </div>
      }</HashRouter>
  );
}

export default App;
